<template>
  <div class="myMerchant">
    <div class="flex myTitle">
      <p @click="goback">
        <i style="font-size:16px;" class="el-icon-arrow-left"></i>
      </p>
      <p style="font-weight: 600;">代理发展的商户</p>
      <p></p>
    </div>
    <!--  -->
    <div>
      <!-- 代理发展的商户 -->
      <div class="flex" style="padding:10px">
        <p v-if="companyType===1">服务商-{{companyName}}</p>
        <p v-if="companyType===2">一级代理-{{companyName}}</p>
        <p v-if="companyType===3">二级代理-{{companyName}}</p>
        <p v-if="companyType===4">三级代理-{{companyName}}</p>
        <p v-if="companyType===-1">超级服务商-{{companyName}}</p>
        <p v-if="companyType!=1&&companyType!=2&&companyType!=3&&companyType!=4&&companyType!=-1">超级服务商-{{companyName}}</p>
        <div class="flex">
          <p style="margin-right:5px;color:#6A6A6A;">
            销售总额
            <i @click="getisorder" :class="isorder?'el-icon-caret-bottom':'el-icon-caret-top'"></i>
          </p>
          <p style="color:#6A6A6A;">
            佣金总额
            <i @click="getisorder2" :class="isorder2?'el-icon-caret-bottom':'el-icon-caret-top'"></i>
          </p>
        </div>
      </div>
      <ul>
        <li class="flex myLi" v-for="(item,index) in agencyDetailList" :key="index">
          <div>
            <p style="font-size:14px;">{{item.storeName}}</p>
            <p class="ywy">业务员：{{item.managerName }}</p>
          </div>
          <div class="flex">
            <div>
              <p>
                <span class="hui">销售总额：</span>
                <span class="hui">￥{{item.countPayPrice }}</span>
              </p>
              <p>
                <span class="hui">佣金总额：</span>
                <span class="lan">￥{{item.CommissionMoney }}</span>
              </p>
            </div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import { myApi } from "@/api";
import * as types from "@/router/types";
import { afterLoginInfoLocal } from "@/storage";
export default {
  data() {
    return {
      payPriceType: 0,
      commissionType: 0,
      isorder: true,
      isorder2: true,
      agencyDetailList: [],
      companyId: this.$route.query.companyId,
      companyName: this.$route.query.companyName,
      companyType: this.$route.query.companyType
    };
  },
  created() {
    this.getagencyDetail();
    switch (this.companyType) {
      case "2":
        this.companyType = "一级代理";
        break;
      case "3":
        this.companyType = "二级代理";
        break;
      case "4":
        this.companyType = "三级代理";
        break;
    }
  },
  methods: {
    getisorder(){
      this.isorder = !this.isorder;
      if(this.isorder){
        this.payPriceType = 0;
      }else{
         this.payPriceType = 1;
      }
      this.getagencyDetail();
    },
     getisorder2(){
      this.isorder2 = !this.isorder2;
      if(this.isorder2){
        this.commissionType = 0;
      }else{
         this.commissionType = 1;
      }
      this.getagencyDetail();
    },
    goback() {
      this.$router.back(-1);
    },
    changeType(index) {
      this.isactive = index;
    },
    //代理的商户详情
    getagencyDetail() {
      myApi
        .agencyDetail(this.companyId, this.payPriceType, this.commissionType)
        .then(
          res => {
            this.agencyDetailList = res.obj.listVoList;
          },
          err => {
            this.$toast.error(err.msg);
          }
        );
    }
  }
};
</script>
<style scoped>
.hui {
  color: #9f9f9f;
}
.lan {
  color: #3791ce;
}
.ywy {
  color: #6f6c6c;
}
.myLi {
  padding: 10px;
  border-bottom: 1px solid #f2f2f2;
}
.myLi p {
  text-align: left;
}
.myType {
  border-bottom: 1px solid #ecebeb;
}
.myType p {
  width: 50%;
  height: 100%;
  padding: 10px;
}
.active {
  color: #3791ce;
  border-bottom: 2px solid #3791ce;
}
.myMerchant {
  background: #fff;
}
.myTitle {
  padding: 10px;
  height: 40px;
}
</style>


